import axios from "axios";
import authHeader from "./auth-header";

const axiosApi = axios.create({
  baseURL: `/api`,
  timeout: 1000,
  headers: authHeader(),
});
export default {
  getPublicContent() {
    return axiosApi.get(`/auth/all`);
  },

  getUserBoard() {
    return axiosApi.get(`/auth/user`);
  },

  getModeratorBoard() {
    return axiosApi.get(`/auth/mod`);
  },

  getAdminBoard() {
    return axiosApi.get(`/auth/admin`);
  },
};

